:root {
  --font-heading: "Courier Prime", monospace;
  --font-body: "Raleway", sans-serif;
  --font-sub: "Roboto Slab", serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark: hsl(40,100%,10%);
  --lt-olive: hsl(88,16%,52%);
  --dk-olive: hsl(82,37%,25%);
  --burlywood: hsl(24,60%,75%);
  --brown: hsl(37,96%,23%);
  --dim-gray: hsl(0,0%,32%);
}

h2 {
  color: var(--lt-olive);
}

.description {
  background-color: var(--dim-gray);
}
.description h2 {
  color: var(--white);
}
.description h2 span {
  animation: glow 1s ease-in-out infinite alternate;
}
@-webkit-keyframes glow {
  from {
    text-shadow: 0 0 10px var(--white), 0 0 20px var(--white), 0 0 30px var(--dim-gray), 0 0 40px var(--dim-gray);
  }
  to {
    text-shadow: 0 0 20px var(--white), 0 0 30px var(--white), 0 0 40px var(--dim-gray), 0 0 50px var(--dim-gray);
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-heading);
  text-transform: uppercase;
  font-weight: 400;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  color: var(--dark);
  font-size: 5rem;
}
@media (max-width: 39.9375em) {
  h1 {
    font-size: 2.8125rem;
  }
}

h2 {
  font-size: 2.375rem;
}
@media (max-width: 39.9375em) {
  h2 {
    font-size: 2rem;
  }
}

p {
  line-height: 1.4;
}

.description h2 {
  font-size: 3rem;
}
@media (max-width: 39.9375em) {
  .description h2 {
    font-size: 2.375rem;
  }
}

.subtitle {
  font-family: var(--font-sub);
  font-size: 2.5rem;
}
@media (max-width: 39.9375em) {
  .subtitle {
    font-size: 1.5rem;
  }
}

#passion p,
#creators p {
  font-size: 1.25rem;
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

header {
  display: flex;
  margin: 0.9375rem auto;
  max-width: 43.75rem;
}

h2 {
  margin-bottom: 0.875rem;
}

div {
  max-width: 67.5rem;
}

section {
  padding: 3rem;
}
@media (max-width: 39.9375em) {
  section {
    padding: 2.8125rem 1.5rem;
  }
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.description h2 {
  margin-bottom: 1.25rem;
  max-width: 46.25rem;
}
.description p {
  max-width: 56.25rem;
}

#benefits {
  display: flex;
  justify-content: center;
}
@media (max-width: 39.9375em) {
  #benefits {
    padding-top: 0;
  }
}

.benefits {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 3.5rem;
}
@media (max-width: 39.9375em) {
  .benefits {
    display: flex;
    flex-direction: column;
  }
}
.benefits p {
  text-align: justify;
}
@media (max-width: 39.9375em) {
  .benefits p {
    text-align: center;
  }
}
.benefits__movement {
  display: grid;
  grid-template-rows: 1fr 2fr;
}
@media (max-width: 39.9375em) {
  .benefits__movement {
    display: flex;
    flex-direction: column;
  }
}
.benefits__movement h2 {
  grid-row: 1;
}
.benefits__movement p {
  grid-row: 2;
}
.benefits__growth {
  display: grid;
  grid-template-rows: 1fr 2fr;
}
@media (max-width: 39.9375em) {
  .benefits__growth {
    display: flex;
    flex-direction: column;
  }
}
.benefits__growth h2 {
  grid-row: 1;
}
@media (max-width: 39.9375em) {
  .benefits__growth h2 {
    padding-top: 2.8125rem;
  }
}
.benefits__growth p {
  grid-row: 2;
}

.roadmap__desktop {
  width: 100%;
  max-width: 64rem;
}
@media (max-width: 39.9375em) {
  .roadmap__desktop {
    display: none;
  }
}
.roadmap__mobile {
  display: none;
}
@media (max-width: 39.9375em) {
  .roadmap__mobile {
    display: flex;
    margin: auto;
    width: 100%;
  }
}

#passion,
#creators {
  display: flex;
  justify-content: center;
  padding: 3rem;
}
@media (max-width: 39.9375em) {
  #passion,
#creators {
    padding: 0 1.5rem 2.8125rem;
  }
}
@media (max-width: 39.9375em) {
  #passion p,
#creators p {
    font-size: 1rem;
  }
}

@media (max-width: 39.9375em) {
  #roadmap {
    padding: 0 0.625rem 2.8125rem;
  }
}

/* Slideshow container */
.slide {
  margin: auto;
  padding-bottom: 0;
}
@media (max-width: 39.9375em) {
  .slide {
    padding: 2.8125rem 0.625rem 0.75rem;
  }
}
.slide__container {
  position: relative;
}
.slide__container--img {
  /* Hide the images by default */
  display: none;
}
.slide__container--img img {
  width: 46.875rem;
  height: 31.25rem;
}
@media (max-width: 39.9375em) {
  .slide__container--img img {
    width: 100%;
    height: 15.625rem;
  }
}

/* Next & previous buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}
@media (max-width: 39.9375em) {
  .prev, .next {
    font-size: 0.75rem;
    padding: 0.625rem;
  }
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.prev {
  left: 0;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

/* Caption text */
.text {
  color: var(--white);
  padding: 0.5rem 0.75rem;
  position: absolute;
  bottom: 0.5rem;
  width: 100%;
  text-align: center;
}
@media (max-width: 39.9375em) {
  .text {
    position: relative;
    color: var(--black);
    font-size: 0.9375rem;
    padding: 0.5rem 0.75rem 0;
    display: block;
  }
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 0.6875rem;
  width: 0.6875rem;
  margin: 0 0.125rem;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}
@media (max-width: 39.9375em) {
  .dot {
    display: none;
  }
}

.active, .dot:hover {
  background-color: #717171;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}