@use '../util' as *;

/* Slideshow container */
.slide {
    margin: auto;
    padding-bottom: 0;
    @include breakpoint-down(small) {
      padding: rem(45) rem(10) rem(12);
    }

    &__container {
        position: relative;

        &--img {
            /* Hide the images by default */
            display: none;

            img {
                width: rem(750);
                height: rem(500);
                @include breakpoint-down(small) {
                  width: 100%;
                  height: rem(250);
                }
            }
        }
    }
}

  
  /* Next & previous buttons */
  .prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    margin-top: -22px;
    padding: 16px;
    color: white;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
    @include breakpoint-down(small) {
      font-size: rem(12);
      padding: rem(10);
    }
  }
  
  /* Position the "next button" to the right */
  .next {
    right: 0;
    border-radius: 3px 0 0 3px;
  }

  .prev {
      left: 0;
  }
  
  /* On hover, add a black background color with a little bit see-through */
  .prev:hover, .next:hover {
    background-color: rgba(0,0,0,0.8);
  }
  
  /* Caption text */
  .text {
    color: var(--white);
    padding: rem(8) rem(12);
    position: absolute;
    bottom: rem(8);
    width: 100%;
    text-align: center;
    @include breakpoint-down(small) {
      position: relative;
      color: var(--black);
      font-size: rem(15);
      padding: rem(8) rem(12) 0;
      display: block;
    }
  }
  
  /* Number text (1/3 etc) */
  .numbertext {
    color: #f2f2f2;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top: 0;
  }
  
  /* The dots/bullets/indicators */
  .dot {
    cursor: pointer;
    height: rem(11);
    width: rem(11);
    margin: 0 rem(2);
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
    @include breakpoint-down(small) {
      display: none;
    }
  }
  
  .active, .dot:hover {
    background-color: #717171;
  }
  
  /* Fading animation */
  .fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }
  
  @-webkit-keyframes fade {
    from {opacity: .4}
    to {opacity: 1}
  }
  
  @keyframes fade {
    from {opacity: .4}
    to {opacity: 1}
  }